import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'home',
      meta: {
        title: '首页'
      },
      component: () => import('@/pages/home/home.vue')
    },
    {
      path: '/socialsecurity',
      name: 'socialsecurity',
      meta: {
        title: '社保公积金'
      },
      component: () => import('@/pages/socialsecurity/socialsecurity.vue')
    },
    {
      path: '/livingpayment',
      name: 'livingpayment',
      meta: {
        title: '生活缴费'
      },
      component: () => import('@/pages/livingpayment/livingpayment.vue')
    }
  ]
});
router.beforeEach((to, from, next) => {
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title;
  }
  next();
});

export default router;



