// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import './common/remstyle'
import { Button, Icon, Row, Col, Loading   } from 'vant'

Vue.use(Button).use(Icon).use(Row).use(Col).use(Loading)
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  // components: { App },
  render: h => h(App),
  template: '<App/>'
})



